.MainSlider .slick-track {
  margin: 0;
}

.MainSlider {
  width: 100vw;
  height: 100vh;
}

.MainSlider .slick__box01 img {
  width: 100%;
  display: flex;
  justify-content: center;
}

.MainSlider .slick__box {
  width: 100vw;
  height: 100vh;
}
.screen01 .slick__box .screen01_circle {
  position: absolute;
  width: 1000px;
  height: 1000px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  box-shadow: 2px 8px 18px rgb(132, 132, 132);
  margin-left: 470px;
  margin-top: -30px;
}

.MainSlider .slick__box ul {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.MainSlider .slick__box01 ul li:nth-child(1) {
  font-size: 74px;
}

.MainSlider .slick__box01 ul li:nth-child(2) {
  width: 500px;
  height: 10px;
  border-top: 3px solid #f0932b;
  margin: 200px 0px 20px 0px;
}

.MainSlider .slick__box01 ul li:nth-child(3) {
  font-size: 28px;
}

.MainSlider .slick__box01 img {
  width: 100vw;
  height: 100vh;
  object-fit: cover;
}

.MainSlider .slick__box02 img {
  width: 400px;
  height: 400px;
  background-size: cover;
}

.MainSlider .slick__box02 {
  width: 100%;
  display: flex;
}

.MainSlider .slick-next {
  right: 120px;
  z-index: 2;
  background-image: url("data:image/png;base64,");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  display: block;
  width: 50px;
  height: 100px;
}

.MainSlider .slick-prev {
  left: 80px;
  z-index: 2;
  background-image: url("data:image/png;base64,");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  display: block;
  width: 50px;
  height: 100px;
}
.MainSlider .slick-next:before,
.MainSlider .slick-prev:before {
  content: "";
}
